<template>
	<view class="shopDetils_con">
		<view class="tabList justify-content-between" v-if="fixedFlag">
			<view class="cow" :class="tabId == item.id?'black':''" v-for="(item, index) in tabList" :key="index" @tap="changeTab(item.id)">{{item.name}}</view>
		</view>
		<scroll-view scroll-y class="scrollBox" :scroll-into-view="toView" :scroll-with-animation="true" :style="{ height: scrollH + 'px' }"
		 @scroll="scroll" :show-scrollbar="false" :class="fixedFlag?'paddingTop':''">
			<image :src="resUrl" mode="" class="masterImg" id="zero">
			</image>
			<view class="shop">
				<!-- 名字 面积 分享 -->
				<view class="name">
					<view class="left">
						<view class="tit">
							{{title}}
						</view>
						<view class="size">
							<text>项目体量</text>
							<span>{{floorSpace}}㎡</span>
						</view>
					</view>
					<!-- <view class="right">
						<image src="../../static/icon/fenxiang.png" mode=""></image>
						<view>分享</view>
					</view> -->
					<button data-name="shareBtn" open-type="share" class="right" plain="true">
						<image src="../../static/icon/fenxiang.png" mode=""></image>
						<view>分享</view>
					</button>
				</view>
				<!-- 物业类型 楼层 开业时间 -->
				<view class="type justify-content-between">
					<view>
						<view class="t">物业类型</view>
						<view class="b">{{type}}</view>
					</view>
					<view>
						<view class="t">楼层</view>
						<view class="b">{{storeyList.length}}</view>
					</view>
					<view>
						<view class="t">开业时间</view>
						<view class="b">{{startTime}}</view>
					</view>
				</view>
				<!-- 营业时间 地址 -->
				<view class="time">
					<view>
						<text class="grey">营业时间：</text>
						<text>{{workTime}}</text>
					</view>
					<view style="margin-top: 27rpx;">
						<text class="grey">项目地址：</text>
						<text class="d">{{address}}</text>
						<view >
							<view @tap="toMap" style="margin-top: 5rpx;">
								<image src="../../static/icon/chaxunditu.png" mode=""></image>
								<span>查看地图</span>
							</view>

						</view>

					</view>
				</view>
			</view>
			<!-- 项目介绍 -->
			<view class="xmjs" id="one">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">项目介绍</view>
				</view>
				<view>{{info}}</view>
				<!-- <view class="cow">
				<view class="t flex">
					<view class="width">项目定位</view>
					<view>至真至诚 构筑缤纷商业</view>
				</view>
				<view class="b">袈裟大师不好的阖家安康收到货时间开始登记卡上的黄金卡的很煎熬是贺卡上啊</view>
			</view>
			<view class="cow">
				<view class="t flex">
					<view class="width">辐射商圈/社群</view>
					<view>至真至诚 构筑缤纷商业</view>
				</view>
				<view class="b">袈裟大师不好的阖家安康收到货时间开始登记卡上的黄金卡的很煎熬是贺卡上啊</view>
			</view> -->
			</view>
			<!-- 业态图 -->
			<view class="ytt" id="two">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">业态图</view>
				</view>
				<scroll-view scroll-y="true" class="imgScroll">
					<view class="kuai" v-for="(item, index) in storeyList" :key="index" :class="storeyLevel==item.storeyLevel?'select_':''"
					 @tap="chooseStoreyLevel(item.storeyLevel,item.resUrl)">{{item.storeyLevel}}</view>
				</scroll-view>
				<!-- <view class="kuai" v-for="(item, index) in info.storeyList" :key="index" :class="storeyLevel==item.storeyLevel?'select_':''" @tap="chooseStoreyLevel(item.storeyLevel)">{{item.storeyLevel}}</view> -->
				<image :src="yttPath" mode="" class="tu"></image>
			</view>
			<!-- 已入驻平拍 -->
			<view class="yrzpt" id="thr">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">已入驻品牌</view>
				</view>
				<scroll-view class="scrollBox2" scroll-x>
					<view class="list">
						<view v-for="(item, index) in brandList" :key="index">
							<image :src="item.resUrl" mode=""></image>
						</view>
					</view>
				</scroll-view>
			</view>
			<!-- 位置及周边 -->
			<view class="wzjzb" id="four">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">位置及周边</view>
				</view>
				<map id="map" :longitude="lng" :latitude="lat" :markers="markers" scale="15" show-location style="width: 100%; height: 380rpx; margin-top:30rpx;"
				 :enable-zoom="false" :enable-scroll="false"></map>
				<view class="traffic flex">
					<view class="tit flex">
						<view v-for="(item, index) in trafficList" :key="index">
							<text :class="trafficId == item.id?'select':''" @tap="changeTraffic(item.id)">{{item.name}}</text>
						</view>
					</view>

				</view>
				<!-- 地铁站 公交站 教育 小区 -->
				<view class="fenlei">
					<view v-for="(item, index) in trafficItem" :key="index">{{item}}</view>
				</view>
				<view class="kuang">{{title}}</view>
			</view>
		</scroll-view>
		<view class="bottom justify-content-between">
			<view class="flex">
				<button class="kefu" open-type="contact" style="line-height: 1;" @bindcontact="handleContact">
					<image src="../../static/icon/kefu.png" mode=""></image>
					<view>客服</view>
				</button>
				<view class="shoucang" @tap="changeCollect">
					<image src="../../static/icon/shoucang.png" mode="" v-if="isCollect == 0"></image>
					<image src="../../static/icon/shoucang_select.png" mode="" v-if="isCollect == 1"></image>
					<view>收藏</view>
				</view>
			</view>

			<view class="number">920商铺|商业街全部在租{{storeNum}}套商铺</view>
		</view>
	</view>
</template>

<script>
	import QQMapWX from '@/common/qqmap-wx-jssdk.min.js'
    var qqmapsdk;
	export default {
		onLoad(res) {
			qqmapsdk = new QQMapWX({
				key: 'PACBZ-A5HWR-OWLWK-WPDMW-LKO62-EVBMD'
			});
			this.userId = uni.getStorageSync('userInfo').userId
			this.token = uni.getStorageSync('userInfo').token
			this.streetId = res.streetId
			this.getDetails()

		},
		onReady() {
			uni.getSystemInfo({
				success: (res) => {
					this.scrollH = res.windowHeight
				}
			});
		},
		data() {
			return {
				scrollH: 0,
				markers: [],
				trafficList: [{
					name: '地铁站',
					id: 0
				}, {
					name: '公交站',
					id: 1
				}, {
					name: '教育',
					id: 2
				}, {
					name: '小区',
					id: 3
				}],
				trafficId: 0,
				// 下滑出现的导航头
				tabList: [{
						name: '详情',
						id: 0
					},
					{
						name: '介绍',
						id: 1
					},
					{
						name: '业态图',
						id: 2
					},
					{
						name: '品牌',
						id: 3
					},
					{
						name: '位置',
						id: 4
					}
				],
				tabId: 0,
				fixedFlag: false,
				toView: '',
				userId: '',
				streetId: '',
				token: '',
				headline: '920商铺',
				storeyLevel: 1, //业态图楼层
				yttPath: '',
				isCollectFlag: false, //点击收藏与否标识
				resUrl: '',
				title: '',
				floorSpace: '',
				type: '',
				storeyList: [],
				startTime: '',
				workTime: '',
				address: '',
				info: '',
				brandList: [],
				lng: '',
				lat: '',
				isCollect: '',
				storeNum: '',
				trafficItem:[],
				trafficFlag: false,
				mobile:'',
			}
		},
		methods: {
			// 客服的回调
			handleContact(e){
				console.log(e)
			},
			scroll(e) {
				if (e.detail.scrollTop > 500) {
					this.fixedFlag = true
				} else {
					this.fixedFlag = false
				}
			},
			// 下滑出现的导航栏
			changeTab(id) {
				this.tabId = id
				if (id == 0) {
					this.toView = 'zero'
				} else if (id == 1) {
					this.toView = 'one'
				} else if (id == 2) {
					this.toView = 'two'
				} else if (id == 3) {
					this.toView = 'thr'
				} else if (id == 4) {
					this.toView = 'four'
				}
			},
			changeTraffic(id) {
				if(this.trafficFlag) return
				this.trafficFlag = true
				this.trafficId = id
				if(this.trafficId == 0){
					qqmapsdk.search({
						keyword: '地铁站',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 3,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.address+r.title)
							})
							console.log(res.data);
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
							console.log(res);
						},
					});
				}else if(this.trafficId == 1){
					// 公交车站
					qqmapsdk.search({
						keyword: '公交站',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 1,
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.address)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}else if(this.trafficId == 2){
					// 教育
					qqmapsdk.search({
						keyword: '大学',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 5,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.title)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}else if(this.trafficId == 3){
					// 小区
					qqmapsdk.search({
						keyword: '小区',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 10,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.title)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}
				
			},
			toMap() {
				// 要传店铺的经纬度
				uni.navigateTo({
					url: '../map/map?lat=' + this.lat + '&lng=' + this.lng
				})
			},
			// 获取详情
			getDetails() {
				let params = {
					userId: this.userId,
					streetId: this.streetId
				}
				uni.showLoading({
					title: '加载中'
				});
				this.$http.post(this.$api.businessDetails, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						// this.info = res.data.data
						this.title = res.data.data.title
						this.resUrl = res.data.data.resUrl
						this.floorSpace = res.data.data.floorSpace
						this.type = res.data.data.type
						this.storeyList = res.data.data.storeyList
						this.startTime = res.data.data.startTime
						this.workTime = res.data.data.workTime
						this.address = res.data.data.address
						this.info = res.data.data.info
						this.brandList = res.data.data.brandList
						this.lng = res.data.data.lng
						this.lat = res.data.data.lat
						this.mobile = res.data.data.mobile
						this.isCollect = res.data.data.isCollect
						this.storeNum = res.data.data.storeNum

						this.headline = res.data.data.title
						this.yttPath = res.data.data.storeyList[0].resUrl
						uni.setNavigationBarTitle({
							title: this.headline
						});
						this.markers = [{
							id: 1,
							iconPath: "../../static/icon/dingwei.png",
							latitude: this.lat,
							longitude: this.lng,
							width: 17.5,
							height: 25,
							callout: {
								content: '拖动地图精准定位门店',
								borderRadius: 20,
								fontSize: 20
							}
						}]
						qqmapsdk.search({
							keyword: '地铁站',
							location: {
								latitude: this.lat,
								longitude: this.lng
							},
							page_size: 3,
							// filter: '大学',
							success :(res)=> {
								this.trafficItem = []
								res.data.map(r=>{
									this.trafficItem.push(r.address+r.title)
								})
							},
							fail:(res)=> {
							},
						});
						uni.hideLoading()
					}else if(res.data.code == 403){
						let details = {
							details: 1,
							streetId: this.streetId
						}
						uni.setStorageSync('details', details)
						uni.switchTab({
							url: '/pages/mine/mine'
						})
					}else{
						this.toast(res.data.msg)
					}
				})
			},
			// 选择业态图
			chooseStoreyLevel(storeyLevel, path) {
				this.storeyLevel = storeyLevel
				this.yttPath = path
			},
			// 收藏
			changeCollect() {
				if (this.isCollectFlag) return
				this.isCollectFlag = true
				let params = {
					objectId: this.streetId,
					objectType: 0,
					userId: this.userId
				}
				uni.showLoading({
					title: '操作中'
				});
				this.$http.post(this.$api.isCollectChange, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						if (this.isCollect == 0) {
							this.isCollect = 1
							this.toast('收藏成功')

						} else {
							this.isCollect = 0
							this.toast('已取消收藏')
						}
						uni.hideLoading()
						this.isCollectFlag = false
					} else {
						this.isCollectFlag = false
						this.toast(res.data.msg)
					}
				})
			},
			callKefu(){
				uni.makePhoneCall({
					phoneNumber: this.mobile
				});
			}
		}

	}
</script>

<style lang="less" scoped>
