			background: #FFFFFF;
			box-shadow: 0rpx 0rpx 11rpx 3rpx rgba(4, 0, 0, 0.06);
			width: 100%;
			padding: 30rpx 40rpx;
			box-sizing: border-box;
			z-index: 9999;

			.cow {
				font-size: 28rpx;
				font-family: PingFang SC;
				font-weight: 400;
				color: #666666;
			}

			.black {
				font-weight: 500;
				color: #343434;
			}
