			box-sizing: border-box;
		}

		.masterImg {
			width: 750rpx;
			height: 500rpx;
		}

		.shop,
		.xmjs,
		.ytt,
		.yrzpt,
		.wzjzb {
			width: 100%;
			background: #FFFFFF;
