				}
			}
		}

		.xmjs {
			.cow {
				margin-top: 42rpx;

				.t {
					font-size: 28rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #909090;

					.width {
						width: 175rpx;
						font-size: 24rpx;
					}
				}

				.b {
					line-height: 34rpx;
					margin-top: 19rpx;
					margin-left: 175rpx;
					font-size: 28rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #343434;
				}
			}
		}

		.ytt {
			position: relative;

			.imgScroll {
				position: absolute;
				top: 120rpx;
				left: 55rpx;
				height: 300rpx;

				.kuai {
					width: 130rpx;
					height: 100rpx;
					background: #FFFFFF;
					border-bottom: 2rpx solid #EEEEEE;
					text-align: center;
					line-height: 100rpx;
					color: #000000;
				}

				.select_ {
					color: #FFFFFF;
