				margin-top: 30rpx;
			}
		}

		.yrzpt {
			.scrollBox2 {
				.list {
					margin-top: 30rpx;
					display: flex;
					align-items: center;

					>view {
						height: 124rpx;
						padding-right: 15rpx;

						image {
							width: 124rpx;
							height: 124rpx;
						}
					}
				}
